import './Home-body.css'
import HomeBodySeller from './Home-body-seller/Home-body-seller';

import Footer from '../Footer/Footer'

const HomeBody = () => {
    return (
        <>
        
        <div className="body-header-content">
            <div className="body-header-content1">
                <img src={image2} alt="" />
            </div>
            
        </div>
        <HomeBodySeller/>
        <Footer/>
        
        
        </>
    );
}

export default HomeBody;
